require('./assets/styles/index.less');
const faceapi = require('./assets/face-api/face-api.js');
const errorMap = {
  'NotAllowedError': '摄像头已被禁用，请在当前浏览器设置中开启后重试',
  'AbortError': '硬件问题，导致无法访问摄像头',
  'NotFoundError': '未检测到可用摄像头',
  'NotReadableError': '操作系统上某个硬件、浏览器或者网页层面发生错误，导致无法访问摄像头',
  'OverConstrainedError': '未检测到可用摄像头',
  'SecurityError': '摄像头已被禁用，请在系统设置或者浏览器设置中开启后重试',
  'TypeError': '类型错误，未检测到可用摄像头'
};

let currentStep = '0';
const stepMap = {
  '0': {
    status: false,
    text: '请保持光线充足，并正对摄像头'
  },
  '1': {
    status: false,
    text: '步骤一、请进行张嘴操作'
  },
  '2': {
    status: false,
    text: '步骤二、请进行摇头操作'
  },
  '3': {
    status: false,
    text: '步骤三、请进行眨眼操作'
  },
  '99': {
    status: false,
    text: '验证完成'
  }
}

class FaceDetection {
  constructor(options) {
    this.options = Object.assign({
      matchedScore: 0.9,
      mediaSize: {
        width: 400,
        height: 520
      },
      closeEyesScore: 1.04,  // 眼睛闭合程度(两个点y轴比值)
      openMouthScore: 1.09,  // 嘴巴张开程度(两个点y轴比值)
      point31Pos: {x:0, y:0, count:0, score: 0.1} // 记录31点的坐标、移动次数
    }, options);

    this.timer = null;
    this.mediaStreamTrack = null; // 摄像头媒体流

    this.videoEl = document.querySelector('#videoEl'); // 视频区域
    this.trackBoxEl = document.querySelector('#trackBox'); // 人脸框绘制区域
    this.canvasImgEl = document.querySelector('#canvasImg'); // 图片绘制区域
    this.operationEl = document.querySelector('.operation'); // 操作按钮
    this.retryBtnEl = document.querySelector('.js-retry');
    this.compareBtnEl = document.querySelector('.js-compare');
    this.pauseBtnEl = document.querySelector('.js-pause');

    this.tipText = document.querySelector('.tip-text');
    this.imgTest = document.querySelector('#imgTest');
    // 视频流当前这一帧
    this.currentFrameEl = document.createElement('img');

    this.init();
  }

  init() {
    this.resize();
    this.initDetection();
    this.bindEvents();
  }

  // 设置相关容器大小
  resize() {
    const tmp = [this.videoEl, this.canvasImgEl];
    for (let i = 0; i < tmp.length; i++) {
      tmp[i].width = this.options.mediaSize.width;
      tmp[i].height = this.options.mediaSize.height;
    }
    const wraperEl = document.querySelector('.wraper');
    wraperEl.style.width = `${this.options.mediaSize.width}px`;
    wraperEl.style.height = `${this.options.mediaSize.height}px`;
  }

  // 初始化人脸识别
  async initDetection() {
    // 加载模型
    faceapi.loadTinyFaceDetectorModel('./assets/face-api/models');
    // 需要加载以下两个模型才能获取到68点的数据
    faceapi.loadSsdMobilenetv1Model('./assets/face-api/models');
    faceapi.loadFaceLandmarkModel('./assets/face-api/models');

    const mediaOpt = {
      video: true
    };
    // 获取 WebRTC 媒体视频流
    if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
      // 最新标准API
      this.mediaStreamTrack = await navigator.mediaDevices.getUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.webkitGetUserMedia) {
      // webkit内核浏览器
      this.mediaStreamTrack = await navigator.webkitGetUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.mozGetUserMedia) {
      // Firefox浏览器
      this.mediaStreamTrack = await navigator.mozGetUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    } else if (navigator.getUserMedia) {
      // 旧版API
      this.mediaStreamTrack = await navigator.getUserMedia(mediaOpt)
        .catch(this.mediaErrorCallback);
    }
    this.initVideo();
  }

  // 人脸识别弹框的相关事件绑定
  bindEvents() {
    // "重新选择" 按钮
    this.retryBtnEl.onclick = () => {
      if (this.videoEl.paused || this.videoEl.ended) {
        this.hideEl(this.trackBoxEl).hideEl(this.operationEl);
        this.videoEl.play();
      }
    };
    // "开始验证" 按钮
    this.compareBtnEl.onclick = async () => {
      console.log('compareBtnEl.onclick');
      // 获取当前视频流中的图像，并通过 canvas 绘制出来
      this.canvasImgEl.getContext('2d').drawImage(this.videoEl, 0, 0, this.canvasImgEl.width, this.canvasImgEl.height);
      const imageBase64 = this.canvasImgEl.toDataURL('image/png');
      // // image = image.replace('data:image/png;base64,', '');
      const imageEl = document.createElement('img');
      imageEl.src = imageBase64;
      const detectionWithLandmarks = await faceapi.detectSingleFace(imageEl).withFaceLandmarks();
      console.log(detectionWithLandmarks);
      return
    };
    this.pauseBtnEl.onclick = () => {
      this.pauseVideo();
    }
  }

  // 初始化视频流
  initVideo(stream) {
    this.videoEl.onplay = () => {
      this.onPlay();
    };
    this.videoEl.srcObject = this.mediaStreamTrack;
    // setTimeout(() => this.onPlay(), 3000);
  }

  pauseVideo() {
    this.videoEl.pause();
    clearTimeout(this.timer)
    this.timer = null;
  }

  // 获取媒体流错误处理
  mediaErrorCallback(error) {
    if (errorMap[error.name]) {
      alert(errorMap[error.name]);
    }
  }

  // 循环监听扫描视频流中的人脸特征
  onPlay() {
    // 判断视频对象是否暂停结束
    // if (this.videoEl && (this.videoEl.paused || this.videoEl.ended)) {
    //   this.timer = setTimeout(() => this.onPlay(), 1000);
    //   return;
    // }
    switch(currentStep) {
      case '0':
        this.handleStep0();
        break;
      case '1':
        this.handleStep1();
        break;
      case '2':
        this.handleStep2();
        break;
      case '3':
        this.handleStep3();
        break;
      case '99':
        this.handleStep99();
        break;
    }

    this.timer = setTimeout(() => this.onPlay(), 500);
  }

  // 人脸框绘制
  drawFaceBox(dimensions, trackBox, detections, score) {
    this.showEl(trackBox);

    // 修改画布大小 
    trackBox.width = this.options.mediaSize.width;
    trackBox.height = this.options.mediaSize.height;

    const resizedDetections = detections.map(res => res.forSize(trackBox.width, trackBox.height));
   
    // 人脸框绘制参数： 人脸评分 > matchedScore，绘制绿色线框，否则，绘制红色线框。
    const faceBoxOpts = score > this.options.matchedScore ? {
      lineWidth: 2,
      textColor: 'green',
      boxColor: 'green',
      withScore: true
    } : {
      lineWidth: 2,
      textColor: 'red',
      boxColor: 'red',
      withScore: true
    };

    // 侦测到人脸后，绘制人脸线框
    faceapi.drawDetection(trackBox, resizedDetections, faceBoxOpts);
  }

  showEl(el) {
    el.style.visibility = 'visible';
    return this;
  }

  hideEl(el) {
    el.style.visibility = 'hidden';
    return this;
  }

  changeTipText() {
    this.tipText.innerText = stepMap[currentStep].text;
  }

  // 判断闭眼
  isCloseEyes(points) {
    // 眨眼判断思路:
    // 38-42,39-41,44-48,45-47这四对点彼此的y轴方向间距，即上下眼皮贴近
    if (!points || !points.length) {
      return false
    }
    console.log((points[41].y+points[40].y)/(points[37].y+points[38].y),(points[47].y+points[46].y)/(points[43].y+points[44].y));
    if ((points[41].y+points[40].y)/(points[37].y+points[38].y) < this.options.closeEyesScore && (points[47].y+points[46].y)/(points[43].y+points[44].y) < this.options.closeEyesScore) {
      return true
    }
    return false
  }

  isOpenMouth(points) {
    // 张嘴判断思路:
    // 68-62,67-63,66-64这三对点彼此的y轴方向间距，及上下嘴唇
    if (!points || !points.length) {
      return false
    }
    console.log(points[67].y/points[61].y,points[66].y/points[62].y,points[65].y/points[63].y);
    if (points[67].y/points[61].y > this.options.openMouthScore || points[66].y/points[62].y > this.options.openMouthScore || points[65].y/points[63].y > this.options.openMouthScore) {
      return true
    }
    return false
  }

  isShakeHead(points,detection) {
    // 摇头判断思路:
    // 鼻子中心31点移动
    if (!points || !points.length) {
      return false
    }
    if (this.options.point31Pos.x > 0) {
      const absX = Math.abs(points[30].x - this.options.point31Pos.x)/detection.imageWidth;
      const absY = Math.abs(points[30].y - this.options.point31Pos.y)/detection.imageHeight;
      console.log(absX, absY);
      if (absX > this.options.point31Pos.score || absY > this.options.point31Pos.score) {
        this.options.point31Pos.count++;
      }
    }
    this.options.point31Pos.x = points[30].x;
    this.options.point31Pos.y = points[30].y;
    if (this.options.point31Pos.count > 2) {
      // 移动超过两次算摇头
      return true
    }
  }

  isAreaCenter(detection) {
    // 视频宽高为640*480,根据返回的人脸位置确定是否在中间区域
    if (!detection.box) {
      return false
    }
    const box = detection.box;
    console.log(box.left/detection.imageWidth,box.right/detection.imageWidth, box.top/detection.imageHeight );
    if (box.left/detection.imageWidth > 0.3 && box.left/detection.imageWidth < 0.4 && box.right/detection.imageWidth < 0.7 && box.right/detection.imageWidth > 0.6 && box.top/detection.imageHeight > 0.1) {
      return true
    }
  }

  async handleStep0() {
    // 设置 TinyFaceDetector 模型参数：inputSize 输入视频流大小  scoreThreshold 人脸评分阈值
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl, new faceapi.TinyFaceDetectorOptions({
      inputSize: 512,
      scoreThreshold: 0.6
    })).withFaceLandmarks();

    // 判断人脸扫描结果
    console.log(detectionWithLandmarks);
    if (detectionWithLandmarks) {
      // 画布绘制人脸线框
      // this.drawFaceBox(this.videoEl, this.trackBoxEl, [detectionWithLandmarks.detection], detectionWithLandmarks.detection.score)
      if (detectionWithLandmarks.detection && detectionWithLandmarks.detection.score > this.options.matchedScore && this.isAreaCenter(detectionWithLandmarks.detection)) {
        console.log(`检测到人脸，匹配度大于 ${this.options.matchedScore}`);
        currentStep = '1';
        this.changeTipText();
        // this.hideEl(this.operationEl);
        // 人脸符合要求，暂停视频流
        // this.videoEl.pause();
        // TODO 调用后端接口进行身份验证
        return;
      }
    }
  }

  async handleStep3() {
    // 获取68点
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    console.log(detectionWithLandmarks);
    if (detectionWithLandmarks && detectionWithLandmarks.faceLandmarks && detectionWithLandmarks.faceLandmarks.positions && this.isAreaCenter(detectionWithLandmarks.detection) && detectionWithLandmarks.detection._score > this.options.matchedScore) {
      const positions = detectionWithLandmarks.faceLandmarks.positions;
      if (this.isCloseEyes(positions)) {
        console.log('眨眼');
        currentStep = '99';
        this.changeTipText();
      }
    }
  }

  async handleStep1() {
    // 获取68点
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    console.log(detectionWithLandmarks);
    if (detectionWithLandmarks && detectionWithLandmarks.faceLandmarks && detectionWithLandmarks.faceLandmarks.positions && this.isAreaCenter(detectionWithLandmarks.detection) && detectionWithLandmarks.detection._score > this.options.matchedScore) {
      const positions = detectionWithLandmarks.faceLandmarks.positions;
      if (this.isOpenMouth(positions)) {
        currentStep = '2';
        this.changeTipText();
      }
    }
  }

  async handleStep2() {
    const detectionWithLandmarks = await faceapi.detectSingleFace(this.videoEl).withFaceLandmarks();
    console.log(detectionWithLandmarks);
    console.log(this.options.point31Pos.count);
    if (detectionWithLandmarks && detectionWithLandmarks.detection && detectionWithLandmarks.detection.box && this.isShakeHead(detectionWithLandmarks.landmarks.positions,detectionWithLandmarks.detection)) {
      currentStep = '3';
      this.changeTipText();
    }
  }

  handleStep99() {
    this.pauseVideo();
  }

}

module.exports = FaceDetection;